/**
 * レイアウトレイヤー
 * サイト全体の骨組みを定義
 */
/**
 * コンテナレイアウト
 * 最大幅と中央揃えを管理
 */
.l-container {
  width: 100%;
  max-width: 72rem;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
@media (min-width: 768px) {
  .l-container {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

.l-container--narrow {
  max-width: 64rem;
}

/**
 * グリッドレイアウト
 * レスポンシブグリッドシステム
 */
.l-grid {
  display: grid;
  gap: 2rem;
}
.l-grid--cols-1 {
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .l-grid--cols-2 {
    grid-template-columns: repeat(2, 1fr);
  }
}
.l-grid--cols-3 {
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .l-grid--cols-3 {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .l-grid--cols-3 {
    grid-template-columns: repeat(3, 1fr);
  }
}

/**
 * メインスタイルシート
 * PDFLOCSSの順序で各レイヤーをインポート
 */
/**
 * コンポーネントレイヤー
 * 再利用可能なUI要素
 */
/**
 * カードコンポーネント
 * 再利用可能なカードUI
 */
.c-card, .p-portfolio__skill-card, .p-portfolio__work-card, .p-portfolio__note-card {
  background-color: #fff;
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  border: 1px solid #ecebff;
  transition: all 0.3s ease;
}
.c-card:hover, .p-portfolio__skill-card:hover, .p-portfolio__work-card:hover, .p-portfolio__note-card:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

/**
 * バッジコンポーネント
 * タグやラベル表示用
 */
.c-badge {
  display: inline-block;
  padding: 0.25rem 0.75rem;
  background-color: #ecebff;
  color: #494b67;
  font-size: 1rem;
  border-radius: 99em;
  transition: all 0.3s ease;
}
.c-badge:hover {
  background-color: #ff006c;
  color: #fff;
}

.c-badge--small {
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
}

/**
 * ボタンコンポーネント
 * 再利用可能なボタンUI
 */
.c-button {
  display: inline-flex;
  align-items: center;
  padding: 0.75rem 2rem;
  border: 3px solid #ff006c;
  border-radius: 0.75rem;
  font-weight: 600;
  border: 3px solid #ff006c;
}
@media (hover: hover) {
  .c-button {
    transition: all 0.3s ease;
  }
}
.c-button.c-button--outline {
  color: #ff006c;
}
.c-button.c-button--outline:hover {
  background-color: #ff006c;
  color: #fff;
}
.c-button--primary {
  background-color: #ff006c;
  color: #fff;
}
.c-button--primary:hover {
  background-color: #494b67;
  border-color: #494b67;
}
.c-button--primary.c-button--outline {
  color: #ff006c;
}
.c-button--primary.c-button--outline:hover {
  background-color: #ff006c;
  color: #fff;
}
.c-button--secondary {
  background-color: #494b67;
  color: #fff;
}
.c-button--secondary:hover {
  background-color: #494b67;
  border-color: #494b67;
}
.c-button--secondary.c-button--outline {
  border: 3px solid #494b67;
}
.c-button--secondary.c-button--outline:hover {
  background-color: #fff;
  color: #494b67;
}

/**
 * popover
 * 主にツールチップ用
 */
[popover] {
  border: none;
  background: transparent;
  overflow-y: hidden;
}

.popover {
  inset: unset;
  background: #ff006c;
  border: none;
  color: #fff;
  position-area: top span-right;
  margin: 0.5em 0;
  border-radius: 0.5rem;
  padding: 0.8em;
}

/**
 * ユーティリティレイヤー
 * 単機能クラス
 */
.u-hidden {
  display: none;
}

.u-text-center {
  text-align: center;
}

.u-text-primary {
  color: #ff006c;
}

.u-font-en {
  font-family: "Pixelify Sans", "Noto Sans JP", sans-serif;
}

.u-mb-4 {
  margin-bottom: 1rem;
}

.u-mb-6 {
  margin-bottom: 1.5rem;
}

.u-mb-8 {
  margin-bottom: 2rem;
}

.u-mb-16 {
  margin-bottom: 4rem;
}

.u-flex {
  display: flex;
}

.u-flex-col {
  flex-direction: column;
}

.u-items-center {
  align-items: center;
}

.u-justify-between {
  justify-content: space-between;
}

.u-justify-center {
  justify-content: center;
}

.u-transition {
  transition: all 0.3s ease;
}

/**
 * プロジェクトレイヤー
 * ページ固有のスタイル
 */
/**
 * ポートフォリオページ固有スタイル
 * ページ固有の構造と上書きを定義
 */
.p-portfolio {
  min-height: 100vh;
  background-color: #fff;
  color: #494b67;
  font-family: "Noto Sans JP", sans-serif;
}
.p-portfolio p a {
  color: #ff006c;
  text-decoration: underline;
  text-underline-offset: 6px;
  text-decoration-thickness: 2px;
  text-decoration-color: #ff006c;
}
.p-portfolio__nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(4px);
  border-bottom: 1px solid #ecebff;
  z-index: 50;
}
.p-portfolio__nav-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem;
}
.p-portfolio__nav-logo {
  font-weight: 700;
  font-size: 1.875rem;
  letter-spacing: 0.05em;
}
.p-portfolio__nav-links {
  display: flex;
  gap: 2rem;
}
.p-portfolio__nav-link {
  font-size: 1rem;
}
@media (hover: hover) {
  .p-portfolio__nav-link {
    transition: all 0.3s ease;
  }
}
.p-portfolio__nav-link:hover {
  color: #ff006c;
}
.p-portfolio__hero {
  padding-top: 8rem;
  padding-bottom: 6rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
.p-portfolio__hero-title {
  font-size: 3.75rem;
  font-weight: 700;
  line-height: 1.25;
  margin-bottom: 2rem;
  color: #494b67;
}
.p-portfolio__hero-title .u-text-primary {
  font-size: 1.875rem;
}
.p-portfolio__hero-subtitle {
  font-size: 1.25rem;
  margin-bottom: 1.5rem;
  opacity: 0.8;
}
@media (min-width: 768px) {
  .p-portfolio__hero-subtitle {
    font-size: 1.5rem;
  }
}
.p-portfolio__hero-description {
  font-size: 1.125rem;
  max-width: 42rem;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.625;
  opacity: 0.7;
}
.p-portfolio__section {
  padding: 5rem 1.5rem;
  scroll-margin-top: 80px;
}
.p-portfolio__section-title {
  font-size: 3.75rem;
  font-weight: 500;
  font-family: "Pixelify Sans", "Noto Sans JP", sans-serif;
  margin-bottom: 1rem;
  text-align: center;
  position: relative;
}
.p-portfolio__section-title::after {
  content: "";
  display: block;
  width: 4rem;
  height: 0.25rem;
  background-color: #ff006c;
  margin: 0.5rem auto 0;
}
.p-portfolio__section-divider {
  display: none;
}
.p-portfolio__skills {
  background-color: #d5f7ff;
}
.p-portfolio__skill-card {
  margin-bottom: 1.5rem;
}
.p-portfolio__skill-header {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
}
.p-portfolio__skill-icon {
  width: 1.5em;
  height: 1.5em;
  color: #ff006c;
}
.p-portfolio__skill-title {
  font-weight: 600;
  font-size: 1.25rem;
  margin-left: 0.75rem;
}
.p-portfolio__skill-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}
.p-portfolio__skill-tags .c-badge {
  cursor: default;
}
.p-portfolio__works ul.l-grid.l-grid--cols-3 {
  position: relative;
}
.p-portfolio__work-card {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  height: 100%;
}
@media (hover: hover) {
  .p-portfolio__work-card {
    transition: all 0.3s ease;
  }
}
.p-portfolio__work-card:hover {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
}
.p-portfolio__work-card > article {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.p-portfolio__work-content {
  padding: 1rem 0;
  display: flex;
  flex-direction: column;
  flex: 1;
}
.p-portfolio__work-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}
.p-portfolio__work-meta {
  font-size: 1rem;
  opacity: 0.7;
  margin-bottom: 0.75rem;
}
.p-portfolio__work-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 0.75rem;
}
.p-portfolio__work-summary {
  font-size: 1rem;
  opacity: 0.6;
  margin-bottom: 1rem;
}
.p-portfolio__work-solution {
  margin-top: auto;
  padding-top: 1.5rem;
  border-top: 1px solid #ecebff;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.p-portfolio__work-problem {
  flex: 1;
  background-color: #d5f7ff;
  padding: 0.75rem 1rem;
  position: relative;
}
.p-portfolio__work-problem::before {
  content: "";
  position: absolute;
  bottom: -19px;
  left: 0;
  width: 100%;
  height: 20px;
  clip-path: polygon(50% 100%, 100% 0, 0 0);
  background-color: #d5f7ff;
}
.p-portfolio__work-problem-title {
  font-weight: 600;
  color: #ff006c;
  margin-bottom: 0.25rem;
  letter-spacing: 0.1em;
}
.p-portfolio__work-problem-text {
  line-height: 1.625;
  color: #494b67;
  opacity: 0.8;
}
.p-portfolio__work-arrow {
  flex-shrink: 0;
  font-size: 1.25rem;
  color: #ff006c;
  font-weight: 700;
  padding: 0 0.5rem;
}
@media (min-width: 640px) {
  .p-portfolio__work-arrow {
    font-size: 1.5rem;
  }
}
.p-portfolio__work-solution-content {
  flex: 1;
  background-color: #fff;
  border: 2px solid #d5f7ff;
  border-top: none;
  padding: 1.5rem 1rem 0.75rem;
  background-image: repeating-linear-gradient(45deg, transparent, transparent 10px, #d5f7ff 10px, #d5f7ff 12px);
}
.p-portfolio__work-solution-title {
  font-weight: 600;
  color: #ff006c;
  margin-bottom: 0.25rem;
  letter-spacing: 0.1em;
  opacity: 0.7;
}
.p-portfolio__work-solution-text {
  line-height: 1.625;
  color: #494b67;
}
.p-portfolio__work-image {
  aspect-ratio: 16/9;
  overflow: hidden;
}
.p-portfolio__work-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (hover: hover) {
  .p-portfolio__work-image img {
    transition: all 0.3s ease;
  }
}
.p-portfolio__work-card:hover .p-portfolio__work-image img {
  transform: scale(1.05);
}
.p-portfolio__work-overlay {
  position: absolute;
  inset: 0;
  background-color: rgba(255, 255, 255, 0.6);
  backdrop-filter: blur(14px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  border-radius: 0.75rem;
  opacity: 0;
  pointer-events: none;
}
@media (hover: hover) {
  .p-portfolio__work-overlay {
    transition: all 0.3s ease;
  }
}
.p-portfolio__work-card:hover .p-portfolio__work-overlay {
  opacity: 1;
  pointer-events: auto;
  z-index: 1;
  width: fit-content;
  margin: 0 auto;
}
.p-portfolio__work-overlay-title {
  font-weight: 600;
  font-size: 1.25rem;
  margin-bottom: 0.75rem;
  color: #ff006c;
}
.p-portfolio__work-overlay-text {
  font-size: 1rem;
  line-height: 1.625;
}
.p-portfolio__notes {
  background-color: #ecebff;
}
.p-portfolio__flow {
  background-color: #fff;
}
.p-portfolio__flow-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  max-width: 48rem;
  margin: 0 auto;
  counter-reset: flow-counter;
}
.p-portfolio__flow-item {
  display: flex;
  align-items: flex-start;
  padding: 1.5rem;
  padding-left: calc(3rem + 1.5rem + 1.5rem);
  background-size: auto auto;
  background-color: rgb(255, 255, 255);
  background-image: repeating-linear-gradient(45deg, transparent, transparent 10px, #d5f7ff 10px, #d5f7ff 12px);
